@use './mixin/mixin' as *;
@use './setting/setting' as *;
@use './setting/normalize' as *;

body {
  font-family: 'Noto Serif JP', 'ヒラギノ角ゴ ProN', 'Hiragino Kaku Gothic ProN', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  background-color: #E6E2D7;
}

/* =====================
  header
===================== */
.header {
  background-image: url(../img/mainvisual-sp.jpg);
  width: 100vw;
  height: 100vh;
  background-position: center center;
  background-size: cover;
  position: relative;
  .header_wrapper {
    display: flex;
    flex-direction: row-reverse;
    padding: 20px;
    @include pc {
      padding: 60px 80px;
    }
    .header_logo {
      width: 40px;
      margin-left: 63px;
    }
    .navigation {
    
    }
    .navigation_box {
      display: flex;
      flex-direction: row-reverse;
    }
    .navigation_list {
      margin-left: 30px;
      .navigation_list_text {
        color: #fff;
        writing-mode: vertical-rl;
      }
    }
  }
  .button {
    position: absolute;
    bottom: 20px;
    left: 20px;
    background-color: #333;
    width: 77px;
    height: 254px;
    @include pc {
      bottom: 30px;
      left: 40px;
    }
    .button_text {
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      writing-mode: vertical-rl;
      position: relative;
      width: 100%;
      height: 100%;
      &:after {
        content: "";
        border: 1px solid #ccc;
        width: 60px;
        height: 236px;
        position: absolute;
        top: 8px;
      }
    }
  }
}
/* =====================
  END header
===================== */

/* =====================
  news
===================== */
.news {
  width: 100%;
  padding: 0 20px;
  margin-top: 80px;
  color: #000;
  writing-mode: vertical-rl;
  @include pc {
    max-width: 1024px;
    margin: 14% auto;
  }
  .news_title {
    display: flex;
    flex-direction: column;
    margin-left: 28px;
    @include pc { 
      margin-left: 97px;
    }
    .news_title_english {
      font-size: 14px;
      margin-left: 11px;
    }
    .news_title_japanese {
      font-size: 28px;
      @include pc {
        font-size: 36px;
        letter-spacing: 3px;
      }
    }
  }
  .news_box {
    margin-top: 20px;
    @include pc {
      margin-top: 80px;
    }
    .news_list {
      padding: 15px 13.5px;
      border-right: 1px solid #000;
      @include pc {
        padding: 15px 31px;
      }
      &:nth-child(3) {
        border-left: 1px solid #000;
        @include pc {
          border-left: none;
        }
      }
      &:nth-child(5) {
        @include pc {
          border-left: 1px solid #000;
        }
      }
      .news_date {
        font-size: 12px;
        margin-left: 9px;
        display: inline-block;
      }
      .news_text {
        font-size: 16px;
      }
    }
  }
}
/* =====================
  END news
===================== */

/* =====================
  feature
===================== */
  .feature {
    width: 100%;
    margin-top: 80px;
    writing-mode: vertical-rl;
    padding: 0 18px;
    position: relative;
    @include pc {
      max-width: 1024px;
      margin: 14% auto;
    }
    .news_feature_title {
      margin-left: 8px;
      @include pc {
        margin-left: 12px;
      }
      .news_feature_title_japanese {
        font-size: 24px;
        letter-spacing: -1px;
        @include pc {
          font-size: 36px;
        }
      }
      .news_feature_title_english {
        margin-top: 22px;
        font-size: 15px;
        letter-spacing: -0.5px;
      }
    }
    .news_feature_image {
      width: 89%;
      margin-top: 69px;
      @include pc {
        width: 65%;
        margin-top: 100px;
      }
    }
    .news_feature_textArea {
      background-color: #F9E90699;
      position: absolute;
      left: 0;
      bottom: -19px;
      width: 51.5%;
      writing-mode: horizontal-tb;
      padding: 9% 0;
      display: flex;
      justify-content: center;
      @include pc {
        left: 28.5%;
        bottom: -3.3%;
        width: 35%;
        padding: 4.8% 0;
      }
      .news_feature_text {
        font-size: 14px;
        line-height: lh(16, 14);
        color: #fff;
        @include pc { 
          font-size: 16px;
          line-height: 1.5;
        }
      }
    }
    &.mod-reverse {
      display: flex;
      flex-direction: column-reverse;
      margin-top: 100px;
      .news_feature_title {
        margin-right: 10px;
        margin-left: 0;
      }
      .news_feature_textArea {
        left: auto;
        right: 0;
        @include pc {
          right: 28.5%;
        }
      }
    }
  }
/* =====================
  END feature
===================== */

/* =====================
  google_map
===================== */
  .google_map {
    margin-top: 100px;
    iframe {
      width: 100%;
      height: 446px;
    }
  }
/* =====================
  END google_map
===================== */

/* =====================
  footer
===================== */
  .footer {
    background-color: #000;
    color: #fff;
    width: 100%;
    padding: 40px 20px 30px;
    writing-mode: vertical-rl;
    display: flex;
    @include pc {
      padding: 80px 80px 30px 30px;
    }
    .footer_wrapper {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
    }
    .footer_title {
      width: 40px;
      margin-left: 31px;
      @include pc {
        margin-left: 66px;
      }
    }
    .footer_address {
      margin-left: 32px;
      @include pc {
        margin-left: 68px;
      }
      .footer_address_text {
        font-size: 14px;
        &:first-of-type {
          margin-left: 12px;
        }
      }
    }
    .footer_list {

      .footer_list_item {
        margin-left: 12px;
        @include pc {
          margin-left: 30px;
        }
        .navigation_list_text {
          color: #fff;
          font-size: 14px;
          @include pc {
            font-size: 16px;
          }
        }
      }
    }
    .footer_contact {
      
      .footer_contact_list {
        color: #fff;
        font-size: 14px;
      }
    }
    .copyright {
      writing-mode: horizontal-tb;
      text-align: center;
      margin-top: 5px;
      font-size: 10px;
    }
  }
/* =====================
  END footer
===================== */